export default{
	name:"zy-search",
	props:{
		isFocus:{	//是否自动获取焦点
			type: Boolean,
			default: false
		},
		theme:{	//选择块级显示还是圆形显示
			type: String,
			default: 'block'
		},
		showWant:{	//是否展示推荐菜单
			type: Boolean,
			default: false
		},
		hotList: { //推荐列表数据
			type: Array,
			default () {
				return []
			}
		},
		speechEngine: { //语音引擎=>讯飞:iFly,百度:'baidu'
			type: String,
			default: 'baidu'
		}
	},
	data() {
		return {
			searchText:'',								//搜索关键词
			hList:uni.getStorageSync('search_cache')		//历史记录
		};
	},
	methods: {
		searchStart: function() {	//触发搜索
			let _this = this;
			if (_this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
			}else{
				_this.$emit('getSearchText', _this.searchText);
				uni.getStorage({
					key:'search_cache',
					success(res){
						let list = res.data;
						if(list.length > 5){
							for(let item of list){
								if(item == _this.searchText){
									return;
								}
							}
							list.pop();
							list.unshift(_this.searchText);
						}else{
							for(let item of list){
								if(item == _this.searchText){
									return;
								}
							}
							list.unshift(_this.searchText);
						}
						_this.hList = list;
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
					},
					fail() {
						_this.hList = [];
						_this.hList.push(_this.searchText);
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
						_this.$emit('getSearchText', _this.searchText);
					}
				})
			}
		},
		keywordsClick (item) {	//关键词搜索与历史搜索
			this.searchText = item;
			this.searchStart();
		},
		delhistory () {		//清空历史记录
			this.hList = [];
			uni.setStorage({
				key: 'search_cache',
				data: []
			});
		},
		startRecognize: function() {	//语音输入
			let _this = this;
			let options = {};
			options.engine = _this.speechEngine;
			options.punctuation = false; // 是否需要标点符号 
			options.timeout = 10 * 1000;
			plus.speech.startRecognize(options, function(s) {
				_this.searchText = _this.searchText + s;
			});
		}
	}
}
